@import url("https://fonts.googleapis.com/css2?family=Jost:wght@300;400;700&family=Kanit&display=swap");


.App {
  text-align: center;
  font-family: "kanit", sans-serif, cursive !important;
}

a{
  color: #ed0000;
  text-decoration: none;
}
a:visited{
    color: #ed0000;
    text-decoration: none;
  }

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
